<template>
  <div class="hello">
    <div class="sanjiaoxing" @click="deleteitem(n)"></div>
    <!-- <h2 @mouseover="$store.commit('showperinfofunc',n)">{{data.name}}</h2> -->
    <h2 @mouseover="data.perinfoflag =!data.perinfoflag">{{data.name}}</h2>
    <br />

    <div class="otheritem">
      <p>
        前端WEB开发 |
        <a :href="data.blog">BLOG</a> |
        <a :href="data.github">GITHUB</a>
      </p>
    </div>
  </div>
</template>

<script>
export default {
  name: "HelloWorld",
  props: {
    data: Object,
    n: Number
  },
  methods: {
    deleteitem(n) {
      this.$confirm("此操作将永久删除该文件, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          this.$store.commit("deleteItem", n);
          this.$message({
            type: "success",
            message: "删除成功!"
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
    }
  }
};
</script>

