<style scoped>
.tool {
  width: 100%;
  height: auto;
  /* background: pink; */
}
.el-form {
  width: 450px;
  position: absolute;
  /* background: rgb(233, 236, 236); */
}
.leftside {
  top: 0;
  left: 5px;
}
.rightside {
  top: 0;
  right: 10px;
}
.bluractive {
  filter: blur(5px);
}
.noblur {
  filter: blur(0px);
}
</style>

<template>
  <div class="tool">
    <el-form :model="form" :rules="rules" class="leftside" ref="form" label-width="90px">
      <h2>基本信息</h2>
      <el-form-item label="姓名:" prop="name">
        <el-input v-model="form.name"></el-input>
      </el-form-item>
      <el-form-item label="年龄:" prop="age">
        <el-input v-model.number="form.age"></el-input>
      </el-form-item>
      <el-form-item label="手机:" prop="tel">
        <el-input v-model.number="form.tel"></el-input>
      </el-form-item>
      <el-form-item label="email:" prop="email">
        <el-input v-model="form.email"></el-input>
      </el-form-item>
      <el-form-item label="求职岗位:" prop="post">
        <el-input v-model="form.post"></el-input>
      </el-form-item>
      <el-form-item label="期望薪资:" prop="salary">
        <el-input v-model="form.salary"></el-input>
      </el-form-item>
      <el-form-item label="工作时间:" prop="workWay">
        <el-radio-group v-model="form.workWay">
          <el-radio label="全职"></el-radio>
          <el-radio label="兼职"></el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item label="github:">
        <el-input v-model="form.github"></el-input>
      </el-form-item>
      <el-form-item label="blog:">
        <el-input v-model="form.blog"></el-input>
      </el-form-item>
      <h2>工作经验</h2>
      <el-form-item label="公司:">
        <el-input v-model="work.company"></el-input>
      </el-form-item>
      <el-form-item label="时间:">
        <el-input v-model="work.time"></el-input>
      </el-form-item>
      <el-form-item label="职位:">
        <el-input v-model="work.post"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="addworkitem">添加工作</el-button>
        <el-button @click="resetwork">重置工作</el-button>
      </el-form-item>
    </el-form>

    <div class="perapp">
      <div class="mainpage" ref="mainpage" :class="isblur[form.perinfoflag]">
        <HelloWorld :data="form" />
        <card1 :data="form" />
        <card2 :data="form" />
        <div class="arstyle">
          <div class="around"></div>
          <div class="around"></div>
          <div class="around"></div>
          <div class="around"></div>
          <div class="around"></div>
          <div class="around"></div>
          <div class="around"></div>
          <div class="around"></div>
          <div class="around"></div>
          <div class="around"></div>
          <div class="around"></div>
          <div class="around"></div>
          <div class="around"></div>
        </div>
      </div>
      <perinfo :data="form" class="animated" :class="peranimate[form.perinfoflag]" />
    </div>

    <el-form
      label-position="left"
      :model="form"
      :rules="rules"
      class="rightside"
      ref="form"
      label-width="80px"
    >
      <h2>教育背景</h2>
      <el-form-item label="学校:">
        <el-input v-model="education.school"></el-input>
      </el-form-item>
      <el-form-item label="时间:">
        <el-input v-model="education.time"></el-input>
      </el-form-item>
      <el-form-item label="专业:">
        <el-input v-model="education.specialty"></el-input>
      </el-form-item>
      <el-form-item size="medium">
        <el-button type="primary" @click="addeduitem">添加学校</el-button>
        <el-button @click="resetedu">重置学校</el-button>
      </el-form-item>
      <h2>工作技能</h2>
      <el-form-item label="技能:">
        <el-input v-model="skill.name"></el-input>
      </el-form-item>
      <el-form-item label="熟练度:">
        <el-slider v-model="skill.Proficiency"></el-slider>
        <!-- <el-input v-model="skill.Proficiency"></el-input> -->
      </el-form-item>
      <el-form-item size="medium">
        <el-button type="primary" @click="addskillitem">添加技能</el-button>
        <el-button @click="resetskill">重置技能</el-button>
      </el-form-item>
      <h2>自我评价</h2>
      <el-form-item prop="evaluation">
        <el-input rows="5" type="textarea" v-model="form.evaluation"></el-input>
      </el-form-item>
      <h2>职业规划</h2>
      <el-form-item prop="careerPlanning">
        <el-input rows="6" type="textarea" v-model="form.careerPlanning"></el-input>
      </el-form-item>

      <el-form-item>
        <el-button type="primary" @click="onSubmit('form')">立即创建</el-button>
        <el-button @click="unSubmit">取消</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
import HelloWorld from "./HelloWorld.vue";
import card1 from "./card1.vue";
import card2 from "./card2.vue";
import perinfo from "./personinfo.vue";

export default {
  components: {
    HelloWorld,
    card1,
    card2,
    perinfo
  },
  data() {
    return {
      isblur: { true: "bluractive", false: "noblur" },
      skill: {
        name: "",
        init: 0,
        Proficiency: 0
      },
      education: { school: "", time: "", specialty: "" },
      work: { company: "", time: "", post: "" },
      peranimate: { true: " jackInTheBox", false: "rollOut" },
      form: {
        perinfoflag: false,
        name: "刘欢",
        age: 27,
        tel: 13627079680,
        email: "liuziyu36@gmail.com",
        post: "WEB前端",
        salary: "5.5k-7.5k",
        workWay: "全职",
        careerObjective: "",
        workExperience: [
          {
            company: "苏州今生有约婚纱摄影",
            time: "2014.7-2015.10",
            post: "婚纱照美化调修"
          },
          {
            company: "苏州万盟玉栈",
            time: "2016.3-2017.102016.3-2017.10",
            post: "设计师"
          },
          {
            company: "赣州米兰婚纱摄影",
            time: "2017.06-2018.05",
            post: "婚纱照美化调修"
          },
          {
            company: "喜福文化传播有限公司",
            time: "2018.10-2019.07",
            post: "婚纱照美化调修"
          }
        ],
        education: [
          {
            school: "九江职业技术学院",
            time: "2011.9~2014.6",
            specialty: "动漫设计与制作"
          }
        ],
        evaluation:
          "学习能力强,有良好的学习习惯，遇到不懂，意味着新的学习收获适应能力强，有良好的团队精神和团队意识，能积极融团队, 乐于接受和适应新鲜事物对待工作认真负责，能在岗位和团队中汲取养分不断学习，和公司共同进步和成长",
        careerPlanning:
          "从毕业到现在做了几年的影楼和设计行业,感觉这些好像并不适合我,因为并不能从中获取成就感,后来接触了编程,从开始的基本的html css js学起慢慢的从中找到了乐趣,便开始计划转行做前端,后来又系统的学习了vue 前端框架, nodejs, python 期间自己也写了些项目以便更深入的学习,从开始到现在学编程也有将近一年的时间了,便想着是时候找个公司来检验我的学习成果了.",
        skill: [
          {
            name: "photoshop",
            init: 0,
            Proficiency: 85
          },
          {
            name: "illustrator",
            init: 0,
            Proficiency: 80
          },
          {
            name: "html&css",
            init: 0,
            Proficiency: 75
          },
          {
            name: "js&nodejs",
            init: 0,
            Proficiency: 65
          },
          {
            name: "python",
            init: 0,
            Proficiency: 60
          },
          {
            name: "vue&vuex&axios",
            init: 0,
            Proficiency: 65
          }
        ],
        blog: "myblog/index.html",
        github: "https://github.com/laughte"
      },
      rules: {
        name: [
          { required: true, message: "请输入姓名", trigger: "blur" },
          { min: 2, max: 9, message: "长度在 2 到 9 个字符", trigger: "blur" }
        ],
        age: [
          { required: true, message: "年龄不能为空" },
          { type: "number", message: "年龄必须为数字值" }
        ],
        post: [{ required: true, message: "内容不能为空", trigger: "blur" }],
        salary: [{ required: true, message: "内容不能为空", trigger: "blur" }],
        tel: [
          { required: true, message: "请输入手机号码", trigger: "blur" },
          { type: "number", message: "号码必须为数字值" }
        ],
        email: [
          { required: true, message: "请输入邮箱地址", trigger: "blur" },
          {
            type: "email",
            message: "请输入正确的邮箱地址",
            trigger: ["blur", "change"]
          }
        ],
        evaluation: [
          { required: true, message: "请输入相关内容", trigger: "blur" },
          {
            min: 50,
            max: 200,
            message: "长度在 50 到 200 个字符",
            trigger: "blur"
          }
        ],
        careerPlanning: [
          { required: true, message: "请输入相关内容", trigger: "blur" },
          {
            min: 50,
            max: 200,
            message: "长度在 50 到 200 个字符",
            trigger: "blur"
          }
        ],
        workWay: [
          { required: true, message: "请选择活工作方式", trigger: "change" }
        ]
      }
    };
  },
  methods: {
    onSubmit(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          //   alert("submit!");
          this.$store.state.personinfo.unshift(this.form);
          this.$store.commit("saveStorageData");
          this.form = {
            perinfoflag: false,
            workExperience: [],
            education: [],
            skill: []
          };
          this.$store.commit("showtool");
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    unSubmit() {
      this.$store.commit("showtool");
    },
    addworkitem() {
      this.form.workExperience.push(this.work);
      this.work = {};
    },
    resetwork() {
      this.form.workExperience = [];
    },
    addeduitem() {
      this.form.education.push(this.education);
      this.education = {};
    },
    resetedu() {
      this.form.education = [];
    },
    addskillitem() {
      this.form.skill.push(this.skill);
      this.skill = { init: 0 };
    },
    resetskill() {
      this.form.skill = [];
    }
  }
};
</script>