      v-show="$store.state.mainpageflag"
      @click="showtool"
    ></i>
    <tool class="animated fadeIn" v-show="$store.state.toolflag" />
    <div class="main animated fadeInDown" v-show="$store.state.mainpageflag">
      <transition name="el-zoom-in-center" :key="i" v-for="(data,i) in $store.state.personinfo">
        <div class="perapp" v-show="cardshowflag">
          <div class="mainpage" ref="mainpage" :class="isblur[data.perinfoflag]">
            <HelloWorld :data="data" :n="i" />
            <card1 :data="data" />
            <card2 :data="data" />
