            </div>
          </div>
          <perinfo
            :data="data"
            :n="i"
            class="animated"
            :class="peranimate[$store.state.personinfo[i].perinfoflag]"
          />
        </div>
      </transition>
    </div>
  </div>
</template>

<script>
import tool from "./components/tool.vue";
import HelloWorld from "./components/HelloWorld.vue";
import card1 from "./components/card1.vue";
import card2 from "./components/card2.vue";
import perinfo from "./components/personinfo.vue";
export default {
  name: "app",
  components: {
    HelloWorld,
    card1,
    card2,
    perinfo,
    tool
  },
  data() {
    return {
      cardshowflag: false,
      peranimate: { true: " jackInTheBox", false: "rollOut" },
      isblur: { true: "bluractive", false: "noblur" }
    };
  },
  methods: {
    showtool() {
      this.$store.commit("showtool");
    },
    changeflag() {
      this.cardshowflag = true;
    }
  },
  computed: {
    linsen() {
      return this.$store.state.personinfo;
    }
  },
  watch: {
    linsen: function(n, o) {
      this.$store.commit("saveStorageData");
    }
  },

  mounted() {
    this.changeflag();
  },
  created() {
    this.$store.commit("readStoragedata");
  }
};
</script>

